import React, { Fragment, useEffect, useState } from 'react';
import Breadcrumb from '../../common/breadcrumb';
import { Edit, Trash2 } from 'react-feather';
import { Button, Card, CardBody, Col, Container, Row } from 'reactstrap';
import { _productlist, _deleteproduct } from '../../../services/api/product';
import { toast, ToastContainer } from 'react-toastify';
import { Link } from 'react-router-dom';

const Product_list = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    loadList();
  }, []);
  const loadList = async () => {
    _productlist({}, function (error, response) {
      if (response !== null) {
        setData(response);
      }
    });
  };
  const deleteProduct = async (id) => {
    id !== null &&
      _deleteproduct({ id: id }, function (error, response) {
        if (response !== null) {
          loadList();
          toast.success('Successfully Deleted Product !');
        }
      });
  };
  return (
    <Fragment>
      <Breadcrumb title='Product List' parent='Physical' />
      <ToastContainer />
      <Container fluid={true}>
        <Row className='products-admin ratio_asos'>
          {data.map((myData, i) => {
            return (
              <Col xl='3' sm='6' key={i}>
                <Card>
                  <div className='products-admin'>
                    <CardBody className='product-box'>
                      <div className='img-wrapper'>
                        <div className='lable-block'>
                          {myData.tag === 'new' ? (
                            <span className='lable3'>{myData.tag}</span>
                          ) : (
                            ''
                          )}
                          {myData.discount === 'on sale' ? (
                            <span className='lable4'>{myData.discount}</span>
                          ) : (
                            ''
                          )}
                        </div>
                        <div className='front'>
                          <a href='/#' className='bg-size'>
                            <img
                              alt=''
                              className='img-fluid blur-up bg-img lazyloaded'
                              src={'/fileserver/product/' + myData.images[0]}
                            />
                          </a>
                          <div className='product-hover'>
                            <ul>
                              <li>
                                {myData._id && (
                                  <Button color='btn' type='button'>
                                    <Link
                                      to={{
                                        pathname:
                                          '/products/physical/edit-product',
                                        state: { _id: myData._id },
                                      }}>
                                      {' '}
                                      <Edit className='editBtn' />
                                    </Link>
                                  </Button>
                                )}
                              </li>
                              <li>
                                <Button color='btn' type='button'>
                                  <Trash2
                                    className='deleteBtn'
                                    onClick={() => deleteProduct(myData._id)}
                                  />
                                </Button>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                      <div className='product-detail'>
                        <div className='rating'>
                          <i className='fa fa-star'></i>
                          <i className='fa fa-star'></i>
                          <i className='fa fa-star'></i>
                          <i className='fa fa-star'></i>
                          <i className='fa fa-star'></i>
                        </div>
                        <a href='/#'>
                          {' '}
                          <h6>{myData.name_english}</h6>
                        </a>
                        <h4>
                          {myData.price} <del>{myData.offer_price}</del>
                        </h4>
                        <ul className='color-variant'>
                          <li className='bg-light0'></li>
                          <li className='bg-light1'></li>
                          <li className='bg-light2'></li>
                        </ul>
                      </div>
                    </CardBody>
                  </div>
                </Card>
              </Col>
            );
          })}
        </Row>
      </Container>
    </Fragment>
  );
};

export default Product_list;
