import React, { Fragment, useState } from "react"; 
import Breadcrumb from "../../common/breadcrumb";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Button,
} from "reactstrap";
import axios from "axios"; 

const Currency = () => {
	const [formInfo, setFormInfo] = useState({
    englishcurrency: "", 
    arabiccurrency: "", 
    currencysymbol: "", 
    defaultrate: "", 
    status: "1", 
  }); 
  
 

  const handleInputChange = (key) => (event) => {
    setFormInfo({
      ...formInfo,
      [key]: event.target.value,
    });
  }; 
    

  const handleValidSubmit = async (evt) => {
    evt.preventDefault();
    console.log({ formInfo });
    var data = new FormData();
    data.append("englishcurrency", formInfo.englishcurrency);
    data.append("arabiccurrency", formInfo.arabiccurrency);
    data.append("currencysymbol", formInfo.currencysymbol);
    data.append("defaultrate", formInfo.defaultrate);
    data.append("status", formInfo.status); 
    
    try {
      await axios.post("/v1/currency/add", data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      //alert("add success");
      toast.success("Successfully Added Currency !");
    } catch (ex) {
      //alert("add error");
      toast.error("Failure !");
    }
  };


	return (
    <Fragment>
      <Breadcrumb title="Add Currency" parent="Currency" />
      <ToastContainer />
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <CardHeader>
                <h5>Add Currency</h5>
              </CardHeader>
              <CardBody>
                <Row className="product-adding"> 
                  <Col xl="7">
                    <Form
                      className="needs-validation add-product-form"
                      onSubmit={handleValidSubmit}
                    >
                      <div className="form form-label-center">
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Currency English:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.englishcurrency}
                              onChange={handleInputChange("englishcurrency")}
                              className="form-control"
                              name="currency_engilsh"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Currency Arabic:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.arabiccurrency}
                              onChange={handleInputChange("arabiccurrency")}
                              className="form-control"
                              name="currency_arabic"
                              id="validationCustom02"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Currency Symbol:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.currencysymbol}
                              onChange={handleInputChange("currencysymbol")}
                              className="form-control"
                              name="currency_symbol"
                              id="validationCustom03"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup> 
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Default Rate:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.defaultrate}
                              onChange={handleInputChange("defaultrate")}
                              className="form-control"
                              name="default_rate"
                              id="validationCustom04"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup> 
                      </div>
                      <FormGroup className="form-group row">
                        <Label className="col-xl-3 col-sm-4 mb-0">
                          Select Status :
                        </Label>
                        <div className="col-xl-8 col-sm-7">
                          <select
                            value={formInfo.status}
                            onChange={handleInputChange("status")}
                            className="form-control digits"
                            id="exampleFormControlSelect1"
                          >
                            <option value="1">Active</option>
                            <option value="0">Deactive</option> 
                          </select>
                        </div>
                      </FormGroup>  
                      <div className="offset-xl-3 offset-sm-4">
                        <Button type="submit" color="primary">
                          Add Currency
                        </Button>
                        <Button type="button" color="light">
                          Discard
                        </Button>
                      </div>
                    </Form>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Currency;
