import React, { Fragment, useEffect, useState } from 'react';
import { CKEditor } from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import Breadcrumb from '../../common/breadcrumb';
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Button,
} from 'reactstrap';
import { toast, ToastContainer } from 'react-toastify';
import VideoThumbnail from 'react-video-thumbnail'; // use npm published version
import DatePicker from 'react-datepicker';
import ImageUploading from 'react-images-uploading';
import user from '../../../assets/images/user.png';
import { _createproduct } from '../../../services/api/product';
import { _brandslist } from '../../../services/api/brand';
import { _categorylist } from '../../../services/api/category';
import { _countrylist } from '../../../services/api/country';

const Add_product = () => {
  var data = new FormData();
  const inputRef = React.useRef();
  const inputcatelogRef = React.useRef();
  const [source, setSource] = React.useState();
  const [formInfo, setFormInfo] = useState({
    name_english: '',
    name_arabic: '',
    price: '',
    offer_price: '1',
    deal_price: '',
    productCode: '',
    start_date: '',
    end_date: '',
    size: 'small',
    size_des: '',
    model_no: '',
    category_id: '',
    brand_id: '',
    item_condition: '',
    quantity: 1,
    description_english: '',
    description_arabic: '',
    speacification_english: '',
    speacification_arabic: '',
    delivery_option: '',
    location: '',
    country_id: '',
    address_english: '',
    address_arabic: '',
    color: '',
    minimum_order: '',
    credit_available: '',
    origin: '',
    active_status: true,
    images: [],
    videos: [],
    catelog: [],
  });
  const [countrylist, setcountrylist] = useState([]);
  const [categorylist, setcategorylist] = useState([]);
  const [brandlist, setbrandlist] = useState([]);
  const [selectedImg, setSelectedImg] = React.useState();
  const [images, setImages] = React.useState([]);

  const handleInputChange = (key) => (event) => {
    setFormInfo({
      ...formInfo,
      [key]: event.target.value,
    });
  };
  const handleInputChange_Date = (key) => (event) => {
    setFormInfo({
      ...formInfo,
      [key]: event,
    });
  };

  const onDesChange = (_evt, editor) => {
    var newContent = editor.getData();
    setFormInfo({ ...formInfo, description_english: newContent });
  };
  const onDesChange_arabic = (_evt, editor) => {
    var newContent = editor.getData();
    setFormInfo({ ...formInfo, description_arabic: newContent });
  };

  const IncrementItem = () => {
    if (formInfo.quantity < 9) {
      setFormInfo({ ...formInfo, quantity: formInfo.quantity + 1 });
    } else {
      return null;
    }
  };
  const DecreaseItem = () => {
    if (formInfo.quantity > 0) {
      setFormInfo({ ...formInfo, quantity: formInfo.quantity - 1 });
    } else {
      return null;
    }
  };

  const handleValidSubmit = async (evt) => {
    evt.preventDefault();
    for (var key in formInfo) {
      if (key !== 'videos' || 'images' || 'catelog') {
        data.append(key, formInfo[key]);
      }
    }
    formInfo.images.forEach((tag) => data.append('images', tag));
    formInfo.videos.forEach((tag) => data.append('videos', tag));
    formInfo.catelog.forEach((tag) => data.append('catelog', tag));
    try {
      _createproduct(data, function (error, response) {
        if (response !== null) {
          toast.success('Successfully Added Product !');
        } else {
          toast.error('Failed !');
        }
      });
    } catch (err) {
      console.log(err);
    }
  };
  const onChange = (imageList, addUpdateIndex) => {
    // data for submit
    var imgarr =
      imageList.length > 0 &&
      imageList.map((item) => {
        return item.file;
      });
    setFormInfo({ ...formInfo, images: [...imgarr] });
    setImages(imageList);
    imageList && imageList[0]?.data_url
      ? setSelectedImg(imageList[0].data_url)
      : setSelectedImg('');
  };
  const handleImages = (img) => {
    setSelectedImg(img);
  };

  const handleFileChange = (key) => (event) => {
    if (key == 'videos') {
      const file = event.target.files[0];
      const url = URL.createObjectURL(file);
      setSource(url);
    }
    setFormInfo({
      ...formInfo,
      [key]: [event.target.files[0]],
    });
  };

  const handleChoose = (key) => (event) => {
    key == 'videos'
      ? inputRef.current.click()
      : inputcatelogRef.current.click();
  };
  useEffect(() => {
    _categorylist({}, function (error, response) {
      if (error == null) {
        setcategorylist(response);
      } else if (response == null) {
        console.log(' error');
      }
    });
    _countrylist({}, function (error, response) {
      if (error == null) {
        setcountrylist(response);
      } else if (response == null) {
        console.log(' error');
      }
    });
    _brandslist({}, function (error, response) {
      if (error == null) {
        setbrandlist(response);
      } else if (response == null) {
        console.log(' error');
      }
    });
  }, []);
  return (
    <Fragment>
      <Breadcrumb title='Product' parent='Physical' />
      <ToastContainer />
      <Container fluid={true}>
        <Row>
          <Col sm='12'>
            <Card>
              <CardHeader>
                <h5>Add Product</h5>
              </CardHeader>
              <CardBody>
                <Form
                  className='needs-validation add-product-form'
                  onSubmit={handleValidSubmit}>
                  <Row className='product-adding'>
                    <Col xl='5'>
                      <h5>
                        <b>Images:</b>
                      </h5>
                      <div className='add-product'>
                        <ImageUploading
                          multiple
                          value={images}
                          onChange={onChange}
                          maxNumber={6}
                          dataURLKey='data_url'>
                          {({
                            imageList,
                            onImageUpload,
                            onImageRemoveAll,
                            onImageUpdate,
                            onImageRemove,
                            isDragging,
                            dragProps,
                            errors,
                          }) => (
                            <Row>
                              <Col xl='9 xl-50' sm='6 col-9'>
                                <Button
                                  variant='contained'
                                  color='primary'
                                  onClick={() => {
                                    setSelectedImg('');
                                    setImages([]);
                                    onImageUpload();
                                  }}>
                                  Choose Product pictures{' '}
                                </Button>
                                <br />{' '}
                                {errors && (
                                  <div>
                                    {errors.maxNumber && (
                                      <span>
                                        Number of selected images exceed
                                        maxNumber,Select 5 or less
                                      </span>
                                    )}
                                    {errors.acceptType && (
                                      <span>
                                        Your selected file type is not allow
                                      </span>
                                    )}
                                    {errors.maxFileSize && (
                                      <span>
                                        Selected file size exceed maxFileSize
                                      </span>
                                    )}
                                    {errors.resolution && (
                                      <span>
                                        Selected file is not match your desired
                                        String resolution
                                      </span>
                                    )}
                                  </div>
                                )}
                                <br />
                                <div>
                                  <img
                                    src={selectedImg ? selectedImg : ''}
                                    alt=''
                                    className='img-fluid image_zoom_1 blur-up lazyloaded'
                                  />
                                </div>
                              </Col>
                              <Col xl='3 xl-50' sm='6 col-3'>
                                <output id='Filelist'></output>{' '}
                                <ul className='file-upload-product'>
                                  {imageList.map((image, i) => {
                                    return (
                                      <li key={i}>
                                        <div style={{ position: 'relative' }}>
                                          <button
                                            onClick={() => onImageRemove(i)}
                                            type='submit'
                                            class='close'
                                            style={{
                                              right: '-5px',
                                              position: 'absolute',
                                              top: '-10px',
                                            }}>
                                            <span>&times;</span>
                                          </button>{' '}
                                          <img
                                            alt=''
                                            src={
                                              image?.data_url
                                                ? image.data_url
                                                : user
                                            }
                                            onClick={() =>
                                              handleImages(image.data_url)
                                            }
                                            style={{ width: 50, height: 50 }}
                                          />
                                        </div>
                                      </li>
                                    );
                                  })}
                                </ul>
                              </Col>
                            </Row>
                          )}
                        </ImageUploading>
                      </div>
                      <br />
                      <br />
                    </Col>
                    <Col xl='5'>
                      <h5>
                        <b>Video:</b>
                      </h5>
                      <div className='add-product'>
                        <input
                          ref={inputRef}
                          className='img-fluid image_zoom_1 blur-up lazyloaded'
                          type='file'
                          style={{ display: 'none' }}
                          onChange={handleFileChange('videos')}
                          accept='.mp4'
                        />{' '}
                        <Button
                          variant='contained'
                          color='primary'
                          onClick={handleChoose('videos')}>
                          Choose video
                        </Button>
                        <br />
                        <br />
                        <Row>
                          <Col xl='9 xl-50' sm='6 col-9'>
                            {source && (
                              <video
                                className='img-fluid image_zoom_1 blur-up lazyloaded'
                                controls
                                src={source}
                              />
                            )}
                          </Col>
                          <Col xl='3 xl-50' sm='6 col-3'>
                            <div
                              style={{ maxHeight: '110px', maxWidth: '110px' }}>
                              {source && (
                                <VideoThumbnail
                                  videoUrl={source}
                                  thumbnailHandler={(thumbnail) =>
                                    console.log(thumbnail)
                                  }
                                  height={100}
                                  width={100}
                                />
                              )}
                            </div>
                          </Col>
                        </Row>
                      </div>
                    </Col>
                    <Col xl='7'>
                      <div className='form form-label-center'>
                        <FormGroup row>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Catelog:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <input
                              ref={inputcatelogRef}
                              className='img-fluid image_zoom_1 blur-up lazyloaded'
                              type='file'
                              style={{ display: 'none' }}
                              onChange={handleFileChange('catelog')}
                              accept='.pdf'
                            />{' '}
                            <Button
                              variant='contained'
                              color='primary'
                              onClick={handleChoose('catelog')}>
                              Choose catelog
                            </Button>
                            {formInfo.catelog.map((file) => (
                              <Label className='col-xl-3 col-sm-4 mb-0'>
                                {file.name}{' '}
                              </Label>
                            ))}
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Product Name English:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.name_english}
                              onChange={handleInputChange('name_english')}
                              className='form-control'
                              name='name_english'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                          <div className='valid-feedback'>Looks good!</div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Product Name arabic:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.name_arabic}
                              onChange={handleInputChange('name_arabic')}
                              className='form-control'
                              name='name_arabic'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>{' '}
                        <div className='valid-feedback'>Looks good!</div>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Category:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <select
                              required
                              value={formInfo.category_id}
                              onChange={handleInputChange('category_id')}
                              className='form-control digits'
                              id='exampleFormControlSelect1'>
                              <option>None</option>{' '}
                              {categorylist &&
                                categorylist.map((item) => {
                                  return (
                                    <option value={item._id}>
                                      {item.name}
                                    </option>
                                  );
                                })}
                            </select>
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Brand:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <select
                              value={formInfo.brand_id}
                              required
                              onChange={handleInputChange('brand_id')}
                              className='form-control digits'
                              id='exampleFormControlSelect1'>
                              <option>None</option>{' '}
                              {brandlist &&
                                brandlist.map((item) => {
                                  return (
                                    <option value={item._id}>
                                      {item.englishBrands}
                                    </option>
                                  );
                                })}{' '}
                            </select>
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group row'>
                          <Label className='col-xl-3 col-sm-4'>
                            Product Description English:
                          </Label>
                          <div className='col-xl-8 col-sm-7 description-sm'>
                            <CKEditor
                              editor={ClassicEditor}
                              data={formInfo.description_english}
                              onChange={onDesChange}
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group row'>
                          <Label className='col-xl-3 col-sm-4'>
                            Product Description Arabic:
                          </Label>
                          <div className='col-xl-8 col-sm-7 description-sm'>
                            <CKEditor
                              editor={ClassicEditor}
                              data={formInfo.description_arabic}
                              onChange={onDesChange_arabic}
                            />
                          </div>
                        </FormGroup>{' '}
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Speacification English:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.speacification_english}
                              onChange={handleInputChange(
                                'speacification_english',
                              )}
                              className='form-control'
                              name='speacification_english'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Speacification Arabic:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.speacification_arabic}
                              onChange={handleInputChange(
                                'speacification_arabic',
                              )}
                              className='form-control'
                              name='speacification_arabic'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Item condition:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.item_condition}
                              onChange={handleInputChange('item_condition')}
                              className='form-control'
                              name='item_condition'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Model No:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.model_no}
                              onChange={handleInputChange('model_no')}
                              className='form-control'
                              name='model_no'
                              id='validationCustom01'
                              type='number'
                              required
                            />
                          </div>
                        </FormGroup>
                        <div className='valid-feedback'>Looks good!</div>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Delivery Option:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.delivery_option}
                              onChange={handleInputChange('delivery_option')}
                              className='form-control'
                              name='delivery_option'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>{' '}
                          <div className='valid-feedback'>Looks good!</div>
                        </FormGroup>{' '}
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Country:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <select
                              value={formInfo.country_id}
                              onChange={handleInputChange('country_id')}
                              className='form-control digits'
                              id='exampleFormControlSelect1'
                              required>
                              <option>None</option>{' '}
                              {countrylist &&
                                countrylist.map((item) => (
                                  <option value={item._id}>
                                    {item.englishname}
                                  </option>
                                ))}{' '}
                            </select>
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Location:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.location}
                              onChange={handleInputChange('location')}
                              className='form-control'
                              name='location'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Address English:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.address_english}
                              onChange={handleInputChange('address_english')}
                              className='form-control'
                              name='address_english'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Address Arabic:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.address_arabic}
                              onChange={handleInputChange('address_arabic')}
                              className='form-control'
                              name='address_arabic'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Select Size:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <select
                              value={formInfo.size}
                              onChange={handleInputChange('size')}
                              className='form-control digits'
                              id='exampleFormControlSelect1'>
                              <option value='small'>Small</option>
                              <option value='medium'>Medium</option>
                              <option value='large'>Large</option>
                              <option value='xlarge'>Extra Large</option>
                            </select>
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Size:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.size_des}
                              onChange={handleInputChange('size_des')}
                              className='form-control'
                              name='product_location'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Color:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.color}
                              onChange={handleInputChange('color')}
                              className='form-control'
                              name='color'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Minimum Order:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.minimum_order}
                              onChange={handleInputChange('minimum_order')}
                              className='form-control'
                              name='minimum_order'
                              id='validationCustom01'
                              type='number'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Credit Available:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.credit_available}
                              onChange={handleInputChange('credit_available')}
                              className='form-control'
                              name='credit_available'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Origin:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.origin}
                              onChange={handleInputChange('origin')}
                              className='form-control'
                              name='origin'
                              id='validationCustom01'
                              type='text'
                              required
                            />
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Select Status:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <select
                              value={formInfo.active_status}
                              onChange={handleInputChange('active_status')}
                              className='form-control digits'
                              id='exampleFormControlSelect1'
                              required>
                              <option value='1'>Active</option>
                              <option value='0'>Deactive</option>
                            </select>
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Total Products:
                          </Label>
                          <fieldset className='qty-box ml-0'>
                            <div className='input-group bootstrap-touchspin'>
                              <div className='input-group-prepend'>
                                <Button
                                  className='btn btn-primary btn-square bootstrap-touchspin-down'
                                  type='button'
                                  onClick={DecreaseItem}>
                                  <i className='fa fa-minus'></i>
                                </Button>
                              </div>
                              <div className='input-group-prepend'>
                                <span className='input-group-text bootstrap-touchspin-prefix'></span>
                              </div>
                              {console.log(
                                formInfo.quantity,
                                'formInfo.quantity',
                              )}{' '}
                              <Input
                                className='touchspin form-control'
                                type='text'
                                name='quantity'
                                value={formInfo.quantity}
                                onChange={handleInputChange('quantity')}
                              />
                              <div className='input-group-append'>
                                <span className='input-group-text bootstrap-touchspin-postfix'></span>
                              </div>
                              <div className='input-group-append ml-0'>
                                <Button
                                  className='btn btn-primary btn-square bootstrap-touchspin-up'
                                  type='button'
                                  onClick={IncrementItem}>
                                  <i className='fa fa-plus'></i>
                                </Button>
                              </div>
                            </div>
                          </fieldset>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Price:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.price}
                              onChange={handleInputChange('price')}
                              className='form-control mb-0'
                              name='price'
                              id='validationCustom02'
                              type='number'
                              required
                            />
                          </div>
                          <div className='valid-feedback'>Looks good!</div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Offer Price:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.offer_price}
                              onChange={handleInputChange('offer_price')}
                              className='form-control mb-0'
                              name='offer_price'
                              id='validationCustom02'
                              type='number'
                              required
                            />
                          </div>
                          <div className='valid-feedback'>Looks good!</div>
                        </FormGroup>{' '}
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Start Date:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <DatePicker
                              required
                              selected={formInfo.start_date}
                              onChange={handleInputChange_Date('start_date')}
                            />
                          </div>
                          <div className='invalid-feedback offset-sm-4 offset-xl-3'>
                            Please choose Valid date.
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            End Date:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <DatePicker
                              required
                              selected={formInfo.end_date}
                              onChange={handleInputChange_Date('end_date')}
                            />
                          </div>
                          <div className='invalid-feedback offset-sm-4 offset-xl-3'>
                            Please choose Valid date.
                          </div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Deal of Price:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.deal_price}
                              onChange={handleInputChange('deal_price')}
                              className='form-control mb-0'
                              name='deal_price'
                              id='validationCustom02'
                              type='number'
                              required
                            />
                          </div>
                          <div className='valid-feedback'>Looks good!</div>
                        </FormGroup>
                        <FormGroup className='form-group mb-3 row'>
                          <Label className='col-xl-3 col-sm-4 mb-0'>
                            Product Code:
                          </Label>
                          <div className='col-xl-8 col-sm-7'>
                            <Input
                              value={formInfo.productCode}
                              onChange={handleInputChange('productCode')}
                              className='form-control '
                              name='productCode'
                              id='validationCustomUsername'
                              type='number'
                              required
                            />
                          </div>
                          <div className='invalid-feedback offset-sm-4 offset-xl-3'>
                            Please choose Valid Code.
                          </div>
                        </FormGroup>{' '}
                      </div>

                      <div className='offset-xl-3 offset-sm-4'>
                        <Button type='submit' color='primary'>
                          Add
                        </Button>
                        <Button type='button' color='light'>
                          Discard
                        </Button>
                      </div>
                    </Col>
                  </Row>{' '}
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Add_product;
