import React, { Fragment, useEffect, useState } from "react";
import Breadcrumb from "../../common/breadcrumb";
import "react-toastify/dist/ReactToastify.css";
import { data } from "../../../assets/data/category";
import Datatable from "../../common/datatable";
import {
	Button,
	Card,
	CardBody,
	CardHeader,
	Col,
	Container,
	Form,
	FormGroup,
	Input,
	Label,
	Modal,
	ModalBody,
	ModalFooter,
	ModalHeader,
	Row,
} from "reactstrap";
import axios from "axios";

const Category = () => {

	const [data, setData] = useState([])
	useEffect(() => {

		loadList()
	}, [])

	const loadList = async () => {
		const res = await axios.get('/v1/category');
		//console.log({ res })
		setData(res?.data?.data || [])
	}
	const [open, setOpen] = useState(false);

	const onOpenModal = () => {
		setOpen(true);
	};

	const onCloseModal = () => {
		setOpen(false);
	};

	const [formInfo, setFormInfo] = useState({
		name: "",
		parent: "1",
		img: ""
	});
	const [file, setFile] = useState();
	const [dummyimgs, setDummyimgs] = useState([
		{ img: "" },
	]);

	const handleInputChange = (key) => (event) => {
		setFormInfo({
			...formInfo,
			[key]: event.target.value,
		});
	};

	//Image Upload
	const _handleImgChange = (e, i) => {
		e.preventDefault();
		let reader = new FileReader();
		const image = e.target.files[0];
		reader.onload = () => {
			dummyimgs[i].img = reader.result;
			dummyimgs[i].file = image;
			setFile({ file: file });
			setDummyimgs(dummyimgs);
		};
		reader.readAsDataURL(image);
	};

	//Submit Section
	const handleValidSubmit = async (evt) => {
		evt.preventDefault();
		console.log({ formInfo });
		var data = new FormData();
		data.append("name", formInfo.name);
		data.append("parent", formInfo.parent);
		dummyimgs.forEach((img) => {
			if (img.file) {
				data.append("images", img.file);
			}
		});
		try {
			await axios.post("/v1/category/add", data, {
				headers: {
					"Content-Type": "multipart/form-data",
				},
			});
			alert("add success");
		} catch (ex) {
			alert("add error");
		}
	};


	return (

		<Fragment>

			<Breadcrumb title="Category" parent="Physical" />
			{/* <!-- Container-fluid starts--> */}
			<Container fluid={true}>
				<Row>

					<Col sm="12">
						<Card>
							<CardHeader>
								<h5>Products Category</h5>
							</CardHeader>
							<CardBody>
								<div className="btn-popup pull-right">
									<Button
										type="button"
										color="primary"
										onClick={onOpenModal}
										data-toggle="modal"
										data-original-title="test"
										data-target="#exampleModal"
									>
										Add Category
									</Button>

									<Modal isOpen={open} toggle={onCloseModal}>
										<ModalHeader toggle={onCloseModal}>
											<h5
												className="modal-title f-w-600"
												id="exampleModalLabel2"
											>
												Add Physical Product
											</h5>
										</ModalHeader>
										<ModalBody>
											<Form onSubmit={handleValidSubmit}>
												<FormGroup>
													<Label
														htmlFor="recipient-name"
														className="col-form-label"
													>
														Category Name :
													</Label>
													<Input type="text" name="name" className="form-control" value={formInfo.name}
														onChange={handleInputChange("name")} />
												</FormGroup>
												<FormGroup>
													<Label
														htmlFor="recipient-name"
														className="col-form-label"
													>
														Parent :
													</Label>
													<select
														value={formInfo.parent}
														onChange={handleInputChange("parent")}
														className="form-control digits"
														id="exampleFormControlSelect1"
													>
														<option value="1">Parent</option>
														{data.map((myData, i) => {
															if (myData.parent == "1") {

																return (
																	<option value="{myData.name}" key={i}>{myData.name}</option>

																)
															}

														})}
													</select>
												</FormGroup>

												<FormGroup>
													<Label
														htmlFor="message-text"
														className="col-form-label"
													>
														Category Image :
													</Label>
													{dummyimgs.map((res, i) => {
														return (
															<li key={i}>
																<div className="box-input-file">
																	<Input
																		className="upload"
																		type="file"
																		onChange={(e) => _handleImgChange(e, i)}
																	/>
																</div>
															</li>
														);
													})}
												</FormGroup>
												<Button
													type="submit"
													color="primary"
													onClick={() => onCloseModal("VaryingMdo")}
												>
													Save
											</Button>
												<Button
													type="button"
													color="secondary"
													onClick={() => onCloseModal("VaryingMdo")}
												>
													Close
											</Button>
											</Form>
										</ModalBody>
										<ModalFooter>

										</ModalFooter>
									</Modal>
								</div>
								<div className="clearfix"></div>
								<div id="basicScenario" className="product-physical">

									<Datatable
										myData={data}
										multiSelectOption={false}
										pageSize={10}
										pagination={true}
										class="-striped -highlight"
									/>
								</div>
							</CardBody>
						</Card>
					</Col>
				</Row>
			</Container>
			{/* <!-- Container-fluid Ends--> */}
		</Fragment>
	);
};

export default Category;
