import React, { Fragment, useEffect, useState } from "react";
import Breadcrumb from "../../common/breadcrumb";
//import Datatable from "../../common/datatable";
import DataTable from "react-data-table-component";
import { Edit, Trash2 } from "react-feather";
import {Table,  Button, Card, CardBody, Col, Container, Row } from "reactstrap";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Link } from 'react-router-dom';

const Language_list = () => {
  const [data, setData] = useState([])

  //Delte Section
  const _handleDelete = (id) =>{

    try{
        if (window.confirm("Are you sure you wish to delete this myData?")) {
           
            const res = axios.post('/v1/language/delete', {id});  
            //console.log({ res })
        }
        toast.success("Successfully Deleted !");
      }
      catch(error){
        toast.error("Something Wrong !");
        console.log(error)
      }

    };

    //Data Loading Section
  useEffect(() => {
    const loadList = async () => {
      const res = await axios.get('/v1/language');
      console.log({ res })
      setData(res?.data?.data || [])
    }
    loadList()
  }, [])
  console.log({ data })
  return (
    <Fragment>
    <ToastContainer />
      <Breadcrumb title="Language List" parent="Language" />
      <Container fluid={true}>
        <Row className="products-admin ratio_asos">
          
              <Col xl="12" sm="6">
                <Card>
                  <div className="products-admin">
                    <CardBody className="product-box">
                       <div id="basicScenario" className="product-physical">
                 
                        <Table striped>
                          <thead>
                            <tr>
                              <th>#</th>
                              <th>English Title</th>
                              <th>Arabic Title</th>
                              <th>Short Code</th>
                              <th>Status</th>
                              <th>Edit/Delete</th>
                            </tr>
                          </thead>
                          <tbody>
                          {data.map((myData, i) => {

                            return (
                            <tr  key={i}>
                              <th scope="row">{i}</th>
                              <td>{myData.englishtitle}</td> 
                              <td>{myData.arabictitle}</td>                                 
                              <td>{myData.shortcode}</td>                                 
                              <td>{myData.status}</td> 
                              <td>
                              <Link
                                  to={{
                                    pathname: '/masters/language/editlanguage/',
                                    state: { _id: myData._id },
                                  }}>
                                  <i
                                    className='fa fa-pencil'
                                    style={{
                                      width: 35,
                                      fontSize: 20,
                                      padding: 11,
                                      color: 'rgb(40, 167, 69)',
                                    }}></i>
                                </Link>
                              <span key={myData._id}><i className="fa fa-trash" style={{ width: 35, fontSize: 20, padding: 11, color: "#e4566e", }} onClick={_handleDelete.bind(this, myData._id)}></i></span></td>
                            </tr> 
                            );
                          })}
                          
                          </tbody>
                        </Table> 
                      </div>
                    </CardBody>
                  </div>
                </Card>
              </Col>
            
        </Row>
      </Container>
    </Fragment>
  );
};

export default Language_list;
