import React, { Fragment, useState, useEffect } from "react"; 
import Breadcrumb from "../common/breadcrumb";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Button,
} from "reactstrap";
import axios from "axios"; 

const Users = () => {

	const [data, setData] = useState([])

	const [formInfo, setFormInfo] = useState({
	    firstname: "", 
	    lastname: "", 
	    email: "", 
	    password: "", 
	    confirmpassword: "", 
	    mobile: "", 
	    address: "", 
	    country: "", 
	    location: "", 
      role: "seller", 
	    image: "",  
	  }); 
  
 

  const [file, setFile] = useState();
	  const [dummyimgs, setDummyimgs] = useState([
	    { img: "" }, 
	  ]);

	const handleInputChange = (key) => (event) => {
    setFormInfo({
      ...formInfo,
      [key]: event.target.value,
    });
  };

  //Image Upload
	const _handleImgChange = (e, i) => {
	    e.preventDefault();
	    let reader = new FileReader();
	    const image = e.target.files[0];
	    reader.onload = () => {
	      dummyimgs[i].img = reader.result;
	      dummyimgs[i].file = image;
	      setFile({ file: file });
	      setDummyimgs(dummyimgs);
	    };
	    reader.readAsDataURL(image);
	  };
    

  const handleValidSubmit = async (evt) => {
    evt.preventDefault();
    console.log({ formInfo });
    var data = new FormData();
    data.append("firstname", formInfo.firstname);
    data.append("lastname", formInfo.lastname); 
    data.append("email", formInfo.email); 
    data.append("password", formInfo.password); 
    data.append("confirmpassword", formInfo.confirmpassword); 
    data.append("mobile", formInfo.mobile); 
    data.append("address", formInfo.address); 
    data.append("country", formInfo.country); 
    data.append("location", formInfo.location); 
    data.append("role", formInfo.role); 
    dummyimgs.forEach((img) => {
      if (img.file) {
        data.append("images", img.file);
      }
    });
    
    try {
      await axios.post("/v1/users/add", data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      }); 
      toast.success("Successfully Added User !");
    } catch (ex) { 
      toast.error("Failure !");
    }
  };


	return (
    <Fragment>
      <Breadcrumb title="Create User" parent="Users" />
      <ToastContainer />
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <CardHeader>
                <h5>Create Users</h5>
              </CardHeader>
              <CardBody>
                <Row className="product-adding"> 
                  <Col xl="7">
                    <Form
                      className="needs-validation add-product-form"
                      onSubmit={handleValidSubmit}
                    >
                      <div className="form form-label-center">
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            First Name
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.firstname}
                              onChange={handleInputChange("firstname")}
                              className="form-control"
                              name="firstname"
                              id="validationCustom0111"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Last Name
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.lastname}
                              onChange={handleInputChange("lastname")}
                              className="form-control"
                              name="lastname"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Email
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.email}
                              onChange={handleInputChange("email")}
                              className="form-control"
                              name="email"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup> 

                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Password
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.password}
                              onChange={handleInputChange("password")}
                              className="form-control"
                              name="password"
                              id="validationCustom01"
                              type="password"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>

                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Confirm Password
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.confirmpassword}
                              onChange={handleInputChange("confirmpassword")}
                              className="form-control"
                              name="confirmpassword"
                              id="validationCustom01"
                              type="password"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>

                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Mobile
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.mobile}
                              onChange={handleInputChange("mobile")}
                              className="form-control"
                              name="mobile"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>

                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Address
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.address}
                              onChange={handleInputChange("address")}
                              className="form-control"
                              name="address"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>

                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Country
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.country}
                              onChange={handleInputChange("country")}
                              className="form-control"
                              name="country"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>

                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                           Location 
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <Input
                              value={formInfo.location}
                              onChange={handleInputChange("location")}
                              className="form-control"
                              name="location"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>

                        <FormGroup className="form-group row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Role:
                          </Label>
                          <div className="col-xl-6 col-sm-7">
                            <select
                              value={formInfo.role}
                              onChange={handleInputChange("role")}
                              className="form-control digits"
                              id="exampleFormControlSelect1"
                            >
                              <option value="seller">Seller</option>
                              <option value="buyer">Buyer</option> 
                              <option value="admin">Admin</option> 
                            </select>
                          </div>
                        </FormGroup>
						 
            						<FormGroup className="form-group mb-3 row">
            							<Label className="col-xl-3 col-sm-4 mb-0">
            								Category Image :
            							</Label>
            							<div className="col-xl-6 col-sm-7">
            							{dummyimgs.map((res, i) => {
            	                          return (
            	                            <li key={i}>
            	                              <div className="box-input-file">
            	                                <Input
            	                                  className="upload"
            	                                  type="file"
            	                                  onChange={(e) => _handleImgChange(e, i)}
            	                                /> 
            	                              </div>
            	                            </li>
            	                          );
            	                        })}
            	                        </div>
            						</FormGroup>
                      </div>  
                      <div className="offset-xl-3 offset-sm-4">
                        <Button type="submit" color="primary">
                          Save
                        </Button>
                        <Button type="button" color="light">
                          Discard
                        </Button>
                      </div>
                    </Form>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Users;
