import React, { Fragment, useState } from "react"; 
import Breadcrumb from "../../common/breadcrumb";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Button,
} from "reactstrap";
import axios from "axios"; 

const Language = () => {
	const [formInfo, setFormInfo] = useState({
    englishtitle: "", 
    arabictitle: "", 
    shortcode: "",  
    status: "1", 
  }); 
  
 

  const handleInputChange = (key) => (event) => {
    setFormInfo({
      ...formInfo,
      [key]: event.target.value,
    });
  }; 
    

  const handleValidSubmit = async (evt) => {
    evt.preventDefault();
    console.log({ formInfo });
    var data = new FormData();
    data.append("englishtitle", formInfo.englishtitle);
    data.append("arabictitle", formInfo.arabictitle);
    data.append("shortcode", formInfo.shortcode); 
    data.append("status", formInfo.status); 
    
    try {
      await axios.post("/v1/language/add", data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      //alert("add success");
      toast.success("Successfully Added Language !");
    } catch (ex) {
      //alert("add error");
      toast.error("Failure !");
    }
  };


	return (
    <Fragment>
      <Breadcrumb title="Add Language" parent="Language" />
      <ToastContainer />
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <CardHeader>
                <h5>Add Language</h5>
              </CardHeader>
              <CardBody>
                <Row className="product-adding"> 
                  <Col xl="7">
                    <Form
                      className="needs-validation add-product-form"
                      onSubmit={handleValidSubmit}
                    >
                      <div className="form form-label-center">
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Title English:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.englishtitle}
                              onChange={handleInputChange("englishtitle")}
                              className="form-control"
                              name="title_engilsh"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Title Arabic:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.arabictitle}
                              onChange={handleInputChange("arabictitle")}
                              className="form-control"
                              name="title_arabic"
                              id="validationCustom02"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Short Code:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.shortcode}
                              onChange={handleInputChange("shortcode")}
                              className="form-control"
                              name="short_code"
                              id="validationCustom03"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup> 
                      </div>
                      <FormGroup className="form-group row">
                        <Label className="col-xl-3 col-sm-4 mb-0">
                          Select Status :
                        </Label>
                        <div className="col-xl-8 col-sm-7">
                          <select
                            value={formInfo.status}
                            onChange={handleInputChange("status")}
                            className="form-control digits"
                            id="exampleFormControlSelect1"
                          >
                            <option value="1">Active</option>
                            <option value="0">Deactive</option> 
                          </select>
                        </div>
                      </FormGroup>  
                      <div className="offset-xl-3 offset-sm-4">
                        <Button type="submit" color="primary">
                          Add Language
                        </Button>
                        <Button type="button" color="light">
                          Discard
                        </Button>
                      </div>
                    </Form>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Language;
