import React, { Fragment, useEffect, useState } from "react";
import Breadcrumb from "../common/breadcrumb";
//import Datatable from "../../common/datatable";
import DataTable from "react-data-table-component";
import { Edit, Trash2 } from "react-feather";
import {Table,  Button, Card, CardBody, Col, Container, Row } from "reactstrap";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Link } from 'react-router-dom';

const List_user = () => {

	const [data, setData] = useState([])

  //Delte Section
  const _handleDelete = (id) =>{

    try{
        if (window.confirm("Are you sure you wish to delete this myData?")) {
           
            const res = axios.post('/v1/users/delete', {id});  
            //console.log({ res })
        }
        toast.success("Successfully Deleted !");
      }
      catch(error){
        toast.error("Something Wrong !");
        console.log(error)
      }

    };

	  useEffect(() => {
	    const loadList = async () => {
	      const res = await axios.get('/v1/users');
	      console.log({ res })
	      setData(res?.data?.data || [])
	    }
	    loadList()
	  }, [])
	  console.log({ data })


	return (
		<Fragment>
     <ToastContainer />
      <Breadcrumb title="Users List" parent="Users" />
      <Container fluid={true}>
        <Row className="products-admin ratio_asos">
          
              <Col xl="12" sm="6">
                <Card>
                  <div className="products-admin">
                    <CardBody className="product-box">
                       <div id="basicScenario" className="product-physical">
                 
                        <Table striped>
                          <thead>
                            <tr>
                              <th>#</th>
                              <th>First Name</th>
                              <th>Email</th>
                              <th>Mobile</th> 
                              <th>Address</th>
                              <th>Edit/Delete</th>
                            </tr>
                          </thead>
                          <tbody>
                          {data.map((myData, i) => {

                            return (
                            <tr  key={i}>
                              <th scope="row">{i}</th>
                              <td>{myData.firstname}</td>                                 
                              <td>{myData.email}</td>                                                 
                              <td>{myData.mobile}</td>                                                 
                              <td>{myData.address}</td> 
                              <td><Link
                                  to={{
                                    pathname: '/users/edituser/',
                                    state: { _id: myData._id },
                                  }}>
                                  <i
                                    className='fa fa-pencil'
                                    style={{
                                      width: 35,
                                      fontSize: 20,
                                      padding: 11,
                                      color: 'rgb(40, 167, 69)',
                                    }}></i>
                                </Link>
                              <span key={myData._id}><i className="fa fa-trash" style={{ width: 35, fontSize: 20, padding: 11, color: "#e4566e", }} onClick={_handleDelete.bind(this, myData._id)}></i></span></td>
                            </tr> 
                            );
                          })}
                          
                          </tbody>
                        </Table> 
                      </div>
                    </CardBody>
                  </div>
                </Card>
              </Col>
            
        </Row>
      </Container>
    </Fragment>
	);
};

export default List_user;
