import React, { Fragment, useState, useEffect } from "react"; 
import Breadcrumb from "../../common/breadcrumb";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Button,
} from "reactstrap";
import axios from "axios"; 

const Brands = () => {

	const [data, setData] = useState([])
	 useEffect(() => {
	    const loadList = async () => {
	      const res = await axios.get('/v1/brands');
	      console.log({ res })
	      setData(res?.data?.data || [])
	    }
	    loadList() 
	  }, [])   
	 console.log({ data }) 



	const [formInfo, setFormInfo] = useState({
	    englishBrands: "", 
	    arabicBrands: "", 
	    image: "", 
	    parent: "1", 
	    status: "1" 
	  }); 
  
 

  const [file, setFile] = useState();
	  const [dummyimgs, setDummyimgs] = useState([
	    { img: "" }, 
	  ]);

	const handleInputChange = (key) => (event) => {
    setFormInfo({
      ...formInfo,
      [key]: event.target.value,
    });
  };

  //Image Upload
	const _handleImgChange = (e, i) => {
	    e.preventDefault();
	    let reader = new FileReader();
	    const image = e.target.files[0];
	    reader.onload = () => {
	      dummyimgs[i].img = reader.result;
	      dummyimgs[i].file = image;
	      setFile({ file: file });
	      setDummyimgs(dummyimgs);
	    };
	    reader.readAsDataURL(image);
	  };
    

  const handleValidSubmit = async (evt) => {
    evt.preventDefault();
    console.log({ formInfo });
    var data = new FormData();
    data.append("englishBrands", formInfo.englishBrands);
    data.append("arabicBrands", formInfo.arabicBrands);
    data.append("parent", formInfo.Brandssymbol); 
    data.append("status", formInfo.status); 
    dummyimgs.forEach((img) => {
      if (img.file) {
        data.append("images", img.file);
      }
    });
    
    try {
      await axios.post("/v1/brands/add", data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      //alert("add success");
      toast.success("Successfully Added Brands !");
    } catch (ex) {
      //alert("add error");
      toast.error("Failure !");
    }
  };


	return (
    <Fragment>
      <Breadcrumb title="Add Brands" parent="Brands" />
      <ToastContainer />
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <CardHeader>
                <h5>Add Brands</h5>
              </CardHeader>
              <CardBody>
                <Row className="product-adding"> 
                  <Col xl="7">
                    <Form
                      className="needs-validation add-product-form"
                      onSubmit={handleValidSubmit}
                    >
                      <div className="form form-label-center">
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Brands English:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.englishBrands}
                              onChange={handleInputChange("englishBrands")}
                              className="form-control"
                              name="Brands_engilsh"
                              id="validationCustom0111"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Brands Arabic:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.arabicBrands}
                              onChange={handleInputChange("arabicBrands")}
                              className="form-control"
                              name="Brands_arabic"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
							<Label className="col-xl-3 col-sm-4 mb-0">
								 Parent :
							</Label>
							<div className="col-xl-8 col-sm-7">
							<select
	                            value={formInfo.parent}
	                            onChange={handleInputChange("parent")}
	                            className="form-control digits"
	                            id="exampleFormControlSelect1"
	                          >
	                            <option value="1">Parent</option> 
	                            {data.map((myData, i) => { 
			                        if(myData.parent ==="1"){  									
            									return ( 										
            										<option value="{myData.englishBrands}" key={i}>{myData.englishBrands}</option> 	

            									) 
										        } 
										 
								})}
	                          </select>
	                          </div>
						</FormGroup>
						 
						<FormGroup className="form-group mb-3 row">
							<Label className="col-xl-3 col-sm-4 mb-0">
								Category Image :
							</Label>
							<div className="col-xl-8 col-sm-7">
							{dummyimgs.map((res, i) => {
	                          return (
	                            <li key={i}>
	                              <div className="box-input-file">
	                                <Input
	                                  className="upload"
	                                  type="file"
	                                  onChange={(e) => _handleImgChange(e, i)}
	                                /> 
	                              </div>
	                            </li>
	                          );
	                        })}
	                        </div>
						</FormGroup>
                      </div>
                      <FormGroup className="form-group row">
                        <Label className="col-xl-3 col-sm-4 mb-0">
                          Select Status :
                        </Label>
                        <div className="col-xl-8 col-sm-7">
                          <select
                            value={formInfo.status}
                            onChange={handleInputChange("status")}
                            className="form-control digits"
                            id="exampleFormControlSelect1"
                          >
                            <option value="1">Active</option>
                            <option value="0">Deactive</option> 
                          </select>
                        </div>
                      </FormGroup>  
                      <div className="offset-xl-3 offset-sm-4">
                        <Button type="submit" color="primary">
                          Add Brands
                        </Button>
                        <Button type="button" color="light">
                          Discard
                        </Button>
                      </div>
                    </Form>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Brands;
