import React, { Fragment, useEffect, useState } from 'react';
import Breadcrumb from '../../common/breadcrumb';
import { Table, Card, CardBody, Col, Container, Row } from 'reactstrap';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Link } from 'react-router-dom';

const Role_list = () => {
  //hooks
  const [data, setData] = useState([]);

  // Delete particular item
  const _handleDelete = (id) => {
    try {
      if (window.confirm('Are you sure you wish to delete this myData?')) {
        axios.post('/v1/role/delete', { id }).then((response) => {
          if (response.data.success) {
            toast.success('Successfully Deleted !');
            loadList();
            return;
          }
          toast.error('Failed !');
        });
      }
    } catch (error) {
      toast.error('Something Wrong !');
    }
  };

  //load role list
  const loadList = async () => {
    const res = await axios.get('/v1/role');
    setData(res?.data?.data || []);
  };

  useEffect(() => {
    loadList();
  }, []);

  return (
    <Fragment>
      <ToastContainer />
      <Breadcrumb title='Role List' parent='Role' />
      <Container fluid={true}>
        <Row className='products-admin ratio_asos'>
          <Col xl='12' sm='6'>
            <Card>
              <div className='products-admin'>
                <CardBody className='product-box'>
                  <div id='basicScenario' className='product-physical'>
                    <Table striped>
                      <thead>
                        <tr>
                          <th>#</th>
                          <th>English Name</th>
                          <th>Arabic Name</th>
                          <th>Status</th>
                          <th>Edit/Delete</th>
                        </tr>
                      </thead>
                      <tbody>
                        {data.map((myData, i) => {
                          return (
                            <tr key={i}>
                              <th scope='row'>{i}</th>
                              <td>{myData.englishname}</td>
                              <td>{myData.arabicname}</td>
                              <td>{myData.status}</td>
                              <td>
                                <Link
                                  to={{
                                    pathname: '/masters/role/editrole/',
                                    state: { _id: myData._id },
                                  }}>
                                  <i
                                    className='fa fa-pencil'
                                    style={{
                                      width: 35,
                                      fontSize: 20,
                                      padding: 11,
                                      color: 'rgb(40, 167, 69)',
                                    }}></i>
                                </Link>
                                <span key={myData._id}>
                                  <i
                                    className='fa fa-trash'
                                    style={{
                                      width: 35,
                                      fontSize: 20,
                                      padding: 11,
                                      color: '#e4566e',
                                    }}
                                    onClick={() =>
                                      _handleDelete(myData._id)
                                    }></i>
                                </span>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </Table>
                  </div>
                </CardBody>
              </div>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Role_list;
