import React, { Fragment, useEffect, useState } from 'react';
import Breadcrumb from "../../common/breadcrumb";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Button,
} from "reactstrap";
import axios from "axios"; 

const Brands = (props) => {
  //variables
  const id = props.location.state._id;

  const [data, setData] = useState([])
	const [formInfo, setFormInfo] = useState({
	    englishBrands: "", 
	    arabicBrands: "", 
	    image: "", 
	    parent: "1", 
	    status: "1" 
	  }); 

  //load brands list
  const loadList = async () => {
    const res = await axios.post('/v1/brands/gets', { id });
    res?.data?.data &&
      setFormInfo({
        id: id,
        englishBrands: res.data.data.englishBrands,
        arabicBrands: res.data.data.arabicBrands,
        parent: res.data.data.parent, 
        status: res.data.data.status,
      });
  };
  
 

  const [file, setFile] = useState();
	  const [dummyimgs, setDummyimgs] = useState([
	    { img: "" }, 
	  ]);

	const handleInputChange = (key) => (event) => {
    setFormInfo({
      ...formInfo,
      [key]: event.target.value,
    });
  };

  //Image Upload
	const _handleImgChange = (e, i) => {
	    e.preventDefault();
	    let reader = new FileReader();
	    const image = e.target.files[0];
	    reader.onload = () => {
	      dummyimgs[i].img = reader.result;
	      dummyimgs[i].file = image;
	      setFile({ file: file });
	      setDummyimgs(dummyimgs);
	    };
	    reader.readAsDataURL(image);
	  };
    

  const handleValidSubmit = async (evt) => {
    evt.preventDefault();
    try {
      await axios.post('/v1/brands/update', formInfo, {});
      toast.success('Successfully Updated Brands !');
    } catch (err) {
      toast.error('Failed !');
    }
  };

    useEffect(() => {
      loadList();
    }, []);


	return (
    <Fragment>
      <Breadcrumb title="Update Brands" parent="Brands" />
      <ToastContainer />
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <CardHeader>
                <h5>Update Brands</h5>
              </CardHeader>
              <CardBody>
                <Row className="product-Updateing"> 
                  <Col xl="7">
                    <Form
                      className="needs-validation Update-product-form"
                      onSubmit={handleValidSubmit}
                    >
                      <div className="form form-label-center">
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Brands English:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.englishBrands}
                              onChange={handleInputChange("englishBrands")}
                              className="form-control"
                              name="Brands_engilsh"
                              id="validationCustom0111"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
                          <Label className="col-xl-3 col-sm-4 mb-0">
                            Brands Arabic:
                          </Label>
                          <div className="col-xl-8 col-sm-7">
                            <Input
                              value={formInfo.arabicBrands}
                              onChange={handleInputChange("arabicBrands")}
                              className="form-control"
                              name="Brands_arabic"
                              id="validationCustom01"
                              type="text"
                              required
                            />
                          </div>
                          <div className="valid-feedback">Looks good!</div>
                        </FormGroup>
                        <FormGroup className="form-group mb-3 row">
							<Label className="col-xl-3 col-sm-4 mb-0">
								 Parent :
							</Label>
							<div className="col-xl-8 col-sm-7">
							<select
	                            value={formInfo.parent}
	                            onChange={handleInputChange("parent")}
	                            className="form-control digits"
	                            id="exampleFormControlSelect1"
	                          >
	                            <option value="1">Parent</option> 
	                            {data.map((myData, i) => { 
			                        if(myData.parent ==="1"){  									
            									return ( 										
            										<option value="{myData.englishBrands}" key={i}>{myData.englishBrands}</option> 	

            									) 
										        } 
										 
								})}
	                          </select>
	                          </div>
						</FormGroup>
						 
						<FormGroup className="form-group mb-3 row">
							<Label className="col-xl-3 col-sm-4 mb-0">
								Category Image :
							</Label>
							<div className="col-xl-8 col-sm-7">
							{dummyimgs.map((res, i) => {
	                          return (
	                            <li key={i}>
	                              <div className="box-input-file">
	                                <Input
	                                  className="upload"
	                                  type="file"
	                                  onChange={(e) => _handleImgChange(e, i)}
	                                /> 
	                              </div>
	                            </li>
	                          );
	                        })}
	                        </div>
						</FormGroup>
                      </div>
                      <FormGroup className="form-group row">
                        <Label className="col-xl-3 col-sm-4 mb-0">
                          Select Status :
                        </Label>
                        <div className="col-xl-8 col-sm-7">
                          <select
                            value={formInfo.status}
                            onChange={handleInputChange("status")}
                            className="form-control digits"
                            id="exampleFormControlSelect1"
                          >
                            <option value="1">Active</option>
                            <option value="0">Deactive</option> 
                          </select>
                        </div>
                      </FormGroup>  
                      <div className="offset-xl-3 offset-sm-4">
                        <Button type="submit" color="primary">
                          Update Brands
                        </Button>
                        <Button type="button" color="light">
                          Discard
                        </Button>
                      </div>
                    </Form>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Brands;
