import { common } from './utils/core';

export const _categorylist = async (payload: any, callback: any) => {
  try {
    let request = {
      reqmethod: 'GET',
      url: 'category/dropdown',
      param: {},
      reqdata: payload,
    };
    await await common(request)
      .then((response: any) => {
        response.data.statusCode == 200 &&
          callback(null, response.data.responseContents);
      })
      .catch((error) => {
        console.log('error', JSON.stringify(error));
      });
  } catch (e) {
    callback(e.response.data, null);
  }
};
