import React, { Fragment, useEffect, useState } from 'react';
import Breadcrumb from './common/breadcrumb';
import { Table, Card, CardBody, Col, Container, Row } from 'reactstrap';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Link } from 'react-router-dom';

const Enquires_list = () => {
  //hooks
  const [data, setData] = useState([]);

  // Delete particular item
  const _handleDelete = (id) => {
    try {
      if (window.confirm('Are you sure you wish to delete this myData?')) {
        axios.post('/v1/role/delete', { id }).then((response) => {
          if (response.data.success) {
            toast.success('Successfully Deleted !');
            loadList();
            return;
          }
          toast.error('Failed !');
        });
      }
    } catch (error) {
      toast.error('Something Wrong !');
    }
  };

  //load role list
  const loadList = async () => {
    const res = await axios.get('/v1/enquires');
    setData(res?.data?.data || []);
  };

  useEffect(() => {
    loadList();
  }, []);

  return (
    <Fragment>
      <ToastContainer />
      <Breadcrumb title='Enquires List' parent='Enquires' />
      <Container fluid={true}>
        <Row className='products-admin ratio_asos'>
          <Col xl='12' sm='6'>
            <Card>
              <div className='products-admin'>
                <CardBody className='product-box'>
                  <div id='basicScenario' className='product-physical'>
                    <Table striped>
                      <thead>
                        <tr>
                          <th>#</th>
                          <th>Name</th>
                          <th>Email</th>
                          <th>Mobile</th>
                          <th>Companyname</th>
                          <th>Message</th>
                          <th>Address</th>
                        </tr>
                      </thead>
                      <tbody>
                        {data.map((myData, i) => {
                          return (
                            <tr key={i}>
                              <th scope='row'>{i}</th>
                              <td>{myData.firstname}</td>
                              <td>{myData.email}</td>
                              <td>{myData.mobile}</td>
                              <td>{myData.companyname}</td>
                              <td>{myData.message}</td>
                              <td>{myData.address}</td>
                               
                            </tr>
                          );
                        })}
                      </tbody>
                    </Table>
                  </div>
                </CardBody>
              </div>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );
};

export default Enquires_list;
